import React, { useEffect, useState } from 'react'
import { icone, navElements } from '../data'
import styled, { keyframes } from 'styled-components'
import { color } from '../untils/colors'
import { police } from '../untils/police'
import { useContext } from 'react'
import { PathContext } from '../untils/context'

const slideDownAnimation = keyframes`
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
`

const BeforeContainer = styled.div`
  padding: 0;
  margin-left: 0;
  margin-right: 0;
`

const Container = styled.div`
  padding: 0;
`

const Logo = styled.img`
  width: 100%;
`

const Button = styled.button`
  border-color: ${color.homeBtnColor};
  border-radius: 0;
  box-shadow: none;
  &:active {
    outline: none;
  }
`
const Icone = styled.i`
  color: ${color.homeBtnColor};
  font-size: 1.5em;
`

const Onglets = styled.div`
  background-color: ${color.countNumbersColor};
  margin-top: 10px;
  @media (min-width: 1200px) {
    margin-top: 0;
    background-color: transparent;
  }
`

const List = styled.ul`
  padding-left: 10px;
`

function Header() {
  const { homePage } = useContext(PathContext)

  const [top, setTop] = useState(true)

  useEffect(() => {
    function handleScroll() {
      const isTop = window.scrollY === 0
      setTop(isTop ? true : false)
    }

    window.addEventListener('scroll', handleScroll)

    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  const [headerHeight, setHeaderHeight] = useState(20) // Hauteur par défaut pour les grands écrans
  const [windowWidth, setWindowWidth] = useState(window.innerWidth)

  useEffect(() => {
    // Fonction pour mettre à jour la hauteur de l'en-tête en fonction de la taille de l'écran
    const updateHeaderHeight = () => {
      const screenWidth = window.innerWidth
      setWindowWidth(screenWidth) // Met à jour la largeur de l'écran dans l'état
    }

    // Ajoutez un écouteur d'événement pour détecter les changements de taille d'écran
    window.addEventListener('resize', updateHeaderHeight)

    // Nettoyage : supprimez l'écouteur lors de la suppression du composant
    return () => {
      window.removeEventListener('resize', updateHeaderHeight)
    }
  }, [])

  useEffect(() => {
    // Met à jour la hauteur de l'en-tête en fonction de la largeur de l'écran actuelle
    if (windowWidth < 992) {
      setHeaderHeight(50)
    } else if (windowWidth >= 992 && windowWidth < 1200) {
      setHeaderHeight(100)
    } else {
      setHeaderHeight(80)
    }
  }, [windowWidth])

  const handleClick = (event, targetId, targetHref) => {
    event.preventDefault()

    if (targetHref === '/#') {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      })
    } else {
      const targetElement = document.getElementById(targetId)

      if (targetElement) {
        const elementPosition = targetElement.getBoundingClientRect().top
        const offsetPosition = elementPosition - headerHeight

        window.scrollBy({
          top: offsetPosition,
          behavior: 'smooth',
        })
      }
    }
  }

  const Section = styled.div`
    position: fixed;
    z-index: 5;
    background-color: ${top ? 'transparent' : 'white'};
    animation: ${slideDownAnimation} 1s ease-in-out;
    padding: 2% 0%;
    width: 100%;
  `

  const ListItems = styled.li`
    position: relative;
    margin-left: 2%;
    border-bottom: none;
    padding: 2px 0;

    @media (min-width: 992px) {
      position: relative;
      padding-bottom: 0.5%;
      border-bottom: ${(props) =>
        props.ids === 'home' ? `solid 4px ${top ? 'white' : 'black'}` : null};

      &:after {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 1px;
        background-color: white;
        transition: width 300ms;
      }
      &:hover::after {
        width: ${(props) => (props.ids === 'home' ? 0 : '100%')};
        border-bottom: solid 4px ${top ? 'white' : 'black'};
      }
    }

    @media (min-width: 1200px) {
      margin-left: 5px;
    }
  `

  const Links = styled.a`
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: ${police.second};
    color: white;
    &:focus {
      color: ${color.homeBtnColor};
    }

    @media (min-width: 1200px) {
      color: ${top ? 'white' : 'black'};
      &:hover {
        color: ${color.homeBtnColor};
      }

      &:focus {
        color: ${color.homeBtnColor};
      }

      &:active {
        color: ${color.homeBtnColor};
      }
    }
  `

  return (
    <React.Fragment>
      <Section className="row">
        <BeforeContainer>
          <Container className="container">
            <nav className="navbar navbar-expand-xl">
              <div className="container-fluid">
                <a className="navbar-brand" href="/">
                  <Logo src={top ? icone.light : icone.dark} alt="logo" />
                </a>
                <Button
                  className="navbar-toggler"
                  data-bs-toggle="collapse"
                  data-bs-target="#navbar"
                  aria-controls="navbar"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <Icone className="bi bi-list" />
                </Button>
                <Onglets
                  className="collapse navbar-collapse row justify-content-end"
                  id="navbar"
                >
                  <List className="navbar-nav col-xl-11 col-xxl-9 justify-content-end">
                    {navElements.map(({ id, href, to, title }) => (
                      <ListItems ids={id} className="nav-item" key={id}>
                        <Links
                          href={href}
                          className="nav-link"
                          onClick={(e) =>
                            id !== 'blog' && homePage
                              ? handleClick(e, id, href)
                              : null
                          }
                        >
                          {title}
                        </Links>
                      </ListItems>
                    ))}
                  </List>
                </Onglets>
              </div>
            </nav>
          </Container>
        </BeforeContainer>
      </Section>
    </React.Fragment>
  )
}

export default Header
